import { Typography, Box, IconButton, useTheme } from "@mui/material";
import { InfoOutlined, LocalOfferOutlined, CallOutlined, Info } from "@mui/icons-material";
import { useState } from "react";

const getIcon = (step) => {
	switch (step) {
		case "Info":
			return <InfoOutlined />;
		case "Bid":
			return <LocalOfferOutlined />;
		case "Contact":
			return <CallOutlined />;
	}
};

const CustomStep = ({ label, index, length, activeStep, setActiveStep }) => {
	const theme = useTheme();
	return (
		<Box
			sx={{
				cursor: "pointer",
				display: "flex",
				flexDirection: "column",
				alignItems: "center",
				justifyContent: "center",
			}}
		>
			<Box
				sx={{
					position: "relative",
				}}
			>
				<IconButton
					onClick={() => setActiveStep(index)}
					sx={{
						border: "solid 1px",
						borderColor: "divider",
						background: activeStep >= index ? `${theme.gradient.main} !important` : "",
						color: activeStep >= index ? "white !important" : "",
					}}
				>
					{getIcon(label)}
				</IconButton>
				{index !== length - 1 ? <CustomConnector active={activeStep > index} /> : ""}
			</Box>

			<Typography
				variant="body2"
				onClick={() => setActiveStep(index)}
				sx={{ pointer: "cursor" }}
			>
				{label}
			</Typography>
		</Box>
	);
};

const CustomConnector = ({ active }) => {
	const theme = useTheme();
	return (
		<Box
			sx={{
				position: "absolute",
				left: "calc(100% + 3px)",
				top: "calc(50% - 0.25rem)",
				width: "3rem",
				height: "0.5rem",
				borderRadius: "1rem",
				background: active === true ? theme.gradient.main : theme.palette.divider,
			}}
		></Box>
	);
};

const CustomStepper = ({ steps, activeStep, setActiveStep, contents }) => {
	const length = steps.length;
	return (
		<>
			<Box
				sx={{
					display: "flex",
					alignItems: "center",
					gap: "calc(3rem + 6px)",
					justifyContent: "center",
					mb: 3,
				}}
			>
				{steps?.map((label, index) => (
					<CustomStep
						key={label}
						setActiveStep={setActiveStep}
						activeStep={activeStep}
						length={length}
						index={index}
						label={label}
					/>
				))}
			</Box>

			<Box
				sx={{
					p: 3,
					width: "100%",
				}}
			>
				{contents}
			</Box>
		</>
	);
};

export default CustomStepper;
